  body{
      margin: 0px;  
    }
    
    .login {
      display: flex;
      justify-content: center;
      align-items: center; 
      background: #1E1E1E;
      flex-direction: column;
      width: 100%;
      height: 100vh;
      color: rgb(179, 201, 181);
    }
    // h1{
    //   font-size: 50px;
    // }
    .login__container{
      background: rgba(255, 255, 255, 0.1);
      border: 2px solid white;
      padding: 20px 23px 20px;
      border-radius: 60px;
    }
    .login__container--form{
      display: flex;
      flex-direction: column;
    }
    
    .login__container--form label{
      font-size: 14px;
    }
    .login__container--remember-me{
      display: inherit;
      color: white;
      justify-content: space-between;
      margin-top: 10px;
    }
    .login__container--remember-me a{
      color: white;
      font-size: 14px;
      text-decoration: none;
    }
    .login__container--remember-me a:hover{
      font-size: 16px;
      text-decoration: underline;
    }
    .login__container--social-media{
      display: flex;
      flex-flow: column;
      font-size: 14px;
      align-items: center;
      justify-content: center;
      margin-top: 10px;
    }
    .login__container--social-media a{
      color: white;
      font-size: 14px;
      text-decoration: none;   
    }
    .login__container--register {
      display: block;
    }
    a {
      color: white;
      font-size: 16px;
      font-weight: bold;
      font-family: 'Muli', sans-serif;
    }
    
    
    
    .button{
      background: #2B5D30;
      width: 130px;
      height: 30px;
      border: 0px;
      border-radius: 25px;
      color: white;
      font-size: 15px;
      font-weight: bold;
      cursor: pointer;
      font-family: 'Muli', sans-serif;
      justify-self: left;
    }
    
    .input {
      border: 1px solid white;
      border-radius: 30px;
      height: 30px;
      background: whitesmoke;
      margin-bottom: 5px;
      font-weight: bold;
      font-family: 'Muli', sans-serif;
      outline: none;
      font-size: 16px;
      padding: 0px 20px;
    }
    ::placeholder{
      color: grey;
    }
    
    .login__container--register{
      display: block;
    }

    .footer{
      display: flex;
      background: #1E1E1E;
    }
    .footer a{
      color: white;
      margin: 3px;
      text-decoration: none;
    }
    

@media screen and (min-width: 320px){
  .login{
    background-color:  rgba(92, 89, 89, 0);
    color: #2B5D30;
  }
  h1 {
    font-size: 2rem;
  }
  .login__container{
      border: 0px solid black;
      width: 90vw;
      padding: 0px 15px;
      border-radius: 0px;
    }
      h2 {
        font-size: 18px;
        text-align: center;
      }
    .login__container--form {

    }
      .login__container--form label{
        display: flex;
        font-size: 12px;
        position: relative;
        top: -10px;
      }
      h5 {
        font-size: 3vw;
      }
      .input{
        border: 1px solid black;
        background: whitesmoke;
        margin-bottom: 10px;
        height: 50px;
      }
      .login__container--remember-me {
        color: inherit;
      }

      .login__container--social-media a{
        color: #2B5D30;
      }
      .form-link {
        color: #2B5D30;
      }
      
}

@media screen and (min-width: 480px){
  .login{
    background-color:  rgba(92, 89, 89, 0);
    color: #2B5D30;
  }
  h1 {
    font-size: 2rem;
  }
  .login__container{
      border: 0px solid black;
      width: 90vw;
      padding: 0px 15px;
      border-radius: 0px;
    }
      h2 {
        font-size: 18px;
        text-align: center;
      }
    .login__container--form {

    }
      .login__container--form label{
        display: flex;
        font-size: 20px;
        position: relative;
      }
      h5 {
        margin: 2px;
        padding: 6px;

      }
      .input{
        border: 1px solid black;
        background: whitesmoke;
        margin-bottom: 3px;
      }
      .login__container--remember-me {
        color: inherit;
      }
      .button {
        width: 29vw;
        height: 7vw;
        font-size: 3vw;
      }
      .login__container--social-media {
        align-items: flex-start;
      }
      .login__container--social-media a{
        color: #2B5D30;
        font-size: 4vw;
      }
      .form-link {
        color: #2B5D30;
      }
}



